#pv-view-card {
	height: 92.5%;
	width: 99%;
	max-width: unset;
	overflow: hidden;
}
#pv-view-card>.card-menubar {
	position: relative;
}
#pv-view-card>.card-content {
	height: calc(100% - 40px);
	padding: 0px;
}
#pv-view-card>.card-content>.tablerow.header,
.pv-tablerow {
	grid-template-columns: repeat(4, 25%);
}

#pv-view-card>.card-content>.tablerow.header {
	background-color: #efefef;
	padding: 0px;
	height: 40px;
	line-height: 40px;
	font-weight: 600;
	border-bottom: 1px solid #ccc;
}
#pv-view-card>.card-content>.tablerow.header>div {
	border-right: 1px solid #ccc;
}
#pv-view-card>.card-content>.tablerow.header>:last-child {
	border-right: 0px;
}

#pvs-table {
	height: calc(100% - 40px);
}

.pv-tablerow.heating {
	border-bottom: 1px solid #912d2d;
	cursor: pointer;
	font-size: x-large;
	height: fit-content;
}
.pv-tablerow.cooling {
	border-bottom: 1px solid #2d3f91;
	cursor: pointer;
	font-size: x-large;
	height: fit-content;
}
.pv-tablerow.cooling:hover {
	background-color: #b8bdd15e;
}
.pv-tablerow.heating:hover {
	background-color: #d1b8b85e;
}

/*Editor*/
#pv-edit-card {
	min-height: unset;
	height: fit-content;
	width: 50vw;
}
.pv-form-container {
	border: 1px solid #ccc;
	border-radius: 10px;
	background-color: #efefef;
	display: grid;
	grid-template-columns: repeat(2, 49.5%);
	gap: 1%;
	margin-bottom: 10px;
}
.pv-form-container>:first-child {
	background-color: #fafafa;
	border-radius: 10px;
}
.pv-form-container>div {
	padding: 10px;
}
.pv-form-container>:first-child.heating {
	border: 2.5px solid #912d2d;
}
.pv-form-container>:first-child.cooling {
	border: 2.5px solid #2d3f91;
}

.pv-form-item {
	display: grid;
	grid-template-columns: 49% 50%;
	gap: 1%;
	padding: 10px;
	align-items: center;
	justify-content: center;
}
.form-header {
	font-weight: 700;
}
.pv-form-item>.form-info-title {
	font-size: x-large
}
.pv-form-item>input {
	width: 100%;
}
.pv-form-item>.checkbox-cont {
	max-width: unset;
	width: 35px;
	height: 35px;
}