.week-view-cont {
	height: 93%;
	width: 99.25%;
	display: grid;
	grid-template-columns: 50% 50%;
}

.week-view-cont>.week-view {
	max-width: unset;
	width: 100%;
	height: 100%;
	max-height: unset;
}

#check-job-card {
	width: 30vw;
	min-height: unset;
	height: max-content;
}

#check-job-card>.card-content {
	height: calc(100% - 40px);
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.job-check-form-item {
	display: grid;
	grid-template-columns: 40% 60%;
	width: 100%;
	align-items: center;
	justify-content: center;
}
.job-check-form-item>.form-info-title {
	font-size: 30px;
}
.job-check-form-item>input {
	height: 40px;
}
.job-check-form-item>.checkbox-cont {
	max-width: unset;
	height: 35px;
	width: 35px !important;
}

#update-job-button {
	height: 40px;
	font-size: 25px;
	line-height: 40px;
	background-color: #2a9aad;
}
#update-job-button:hover {
	background-color: #257f8f;
}

#projects-list-card {
	width: 98%;
}
#projects-list-card>.card-menubar {
	position: relative;
}
#projects-list-card>.card-content {
	height: calc(100% - 40px);
	padding: 0px;
}
#projects-table>.tablerow,
#projects-list-card>.card-content>.tablerow.header {
	grid-template-columns: repeat(6, 16.6%);
}
#projects-list-card>.card-content>.tablerow.header {
	background-color: #efefef;
	padding: 0px;
	height: 40px;
	line-height: 40px;
	font-weight: 600;
	border-bottom: 1px solid #ccc;
}
#projects-list-card>.card-content>.tablerow.header>div {
	border-right: 1px solid #ccc;
}
#projects-list-card>.card-content>.tablerow.header>:last-child {
	border-right: 0px;
}
.tablerow>div {
	background-color: transparent !important;
	margin: 0px;
}

#projects-table {
	height: calc(100% - 40px);
}
#projects-table>.tablerow {
	height: fit-content;
	border-bottom: 1px solid #ccc;
	cursor: pointer;
}
#projects-table>.tablerow:hover {
	background-color: #fafafa;
}