.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.search-row {
	display: grid;
	grid-template-columns: 25% 50% 25%;
}

/*SEARCH BAR*/
.search-bar {
	margin: 5px;
}
.search-bar>img {
	height: 20px;
	width: 20px;
	position: relative;
	top: 5px;
	left: 30px;
	z-index: 10;
}
.search-bar>input {
	height: 40px;
	width: 50%;
	text-indent: 35px;
}

.side-bar-menu-cont>.side-bar-menu-controls>:first-child>img {
	transform: rotate(180deg) !important;
	transition: 0.5s !important;
}
.side-bar-menu-cont.collapsed>.side-bar-menu-controls>:first-child>img {
	transform: rotate(0deg) !important;
}